const qiniu = require('qiniu-js');
import myconfig from './config'
import axios from 'axios'
export default {
    async wxinit() {
        return new Promise(async (resolve, reject) => {
            function getTimesTamp() {
                return parseInt(new Date().getTime() / 1000) + '';
            }
            function getNonceStr() {
                return Math.random().toString(36).substr(2, 15);
            }
            const getSignature = async function () {
                const signatureRes = await axios({
                    url: `${myconfig.url.feedUrl}/sign`,
                    params: {
                        nonceStr, timestamp
                    },
                    headers: {
                        'content-type': 'application/json'
                    },
                    withCredentials: true,
                })
                return signatureRes.data.signature;
            }
            const nonceStr = getNonceStr();
            const timestamp = getTimesTamp();
            const signature = await getSignature();

            wx.config({
                debug: false, // ,apialertpclogpc
                appId: 'wx9fd6bbc89436a5ee', // 
                timestamp, // 
                nonceStr, // 
                signature,// 
                jsApiList: ['chooseImage', 'previewImage', 'onMenuShareTimeline'] // JS
            });
        })
    },
    async upload(file) {
        return new Promise(async (resolve, reject) => {
            const tokenRes = await axios({
                method: 'post',
                url: `${myconfig.url.feedUrl}/file/transfer`,
                data: {
                    from: myconfig.user._id,
                    filename: file.name
                },
                withCredentials: true
            })
            const { key, token } = tokenRes.data;
            const config = {
                useCdnDomain: true,
                region: qiniu.region.z1
            }
            var observable = qiniu.upload(file, key, token, {}, config)
            var subscription = observable.subscribe({
                next(res) {

                },
                error(err) {
                    reject(err);
                },
                complete(res) {
                    console.log(':', res)
                    resolve(res);
                }
            })
        })
    },
    async checkOauth() {
        if (!myconfig.user._id) {
            //oauth,oauth
            window.location.href = myconfig.url.oauthUrl;
            return false;
        }
        if (myconfig.user.oauth) {
            return true;
        }
        //oauth,oauth
        window.location.href = myconfig.url.oauthUrl;
        return false;
    },
    async  wait(time) {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                resolve("ok");
            }, time);
        });
    },
    //
    parseDate(array) {
        for (const ele of array) {
            if (ele.createdAt.indexOf('-') === -1) {
                continue
            }
            const now = new Date();
            ele.createdAt = new Date(ele.createdAt)
            //10: 9:01,
            const minute = ele.createdAt.getMinutes();

            if (minute < 10) {
                minute = '0' + minute;
            }
            if (now.getDate() - ele.createdAt.getDate() <= 0) {
                //  :::
                ele.createdAt = `${ele.createdAt.getHours()}:${minute}`;
            }
            else if (now.getDate() - ele.createdAt.getDate() <= 1) {
                ele.createdAt = `  ${ele.createdAt.getHours()}:${minute}`;
                //,:
            } else {
                ele.createdAt = `${ele.createdAt.getMonth() + 1}${ele.createdAt.getDate()}  ${ele.createdAt.getHours()}:${minute}`;
                //
            }
        }
        return array;
    },

    //threadneedShield
    parseShield(threads) {
        for (const thread of threads) {
            for (const shield of myconfig.user.shields) {
                if (shield.uid === thread.uid) {
                    thread.needShield = true;
                }
            }
        }
        return threads;
    }

}


